  },
  components: { 'nav-bar': NavBar }
}
</script>

<style lang='scss'>

@import './scss/skeleton';
@import './scss/utils';

body{
  padding:0px;
  margin:0px;
}
.app{
  width:100%;
}

.add_bar{
  margin-leftt: 6em;
}
</style>



